.header {
    width: 100%;
    min-height: 100vh;
    padding: 1rem 1.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;

    background: radial-gradient(ellipse at bottom, #1B2735 0%, #090A0F 100%);
    overflow: hidden;

    &__container {
        align-self: center;
        width: 100%;
        max-width: 1124px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__title {
        font-family: $main_font;

        line-height: 6.1875rem;
        color: $color5;
        letter-spacing: 0.125rem;
        margin-bottom: 2rem;

        @media screen and (max-width: 767px) {
            font-size: 1.875rem;
            line-height: 3.1875rem;
        }

        @media screen and (min-width: 768px) {
            font-size: 3.4rem;
            line-height: 4.1875rem;
        }

        @media screen and (min-width: 1024px) {
            font-size: 4.7375rem;
            line-height: 6.1875rem;
        }
    }

    &__text {
        font-family: $main_font;

        line-height: 3.625rem;
        color: $color4;
        letter-spacing: 0.125rem;
        margin-bottom: 10rem;

        @media screen and (max-width: 767px) {
            font-size: 2.175rem;

            margin-bottom: 10rem;
        }

        @media screen and (min-width: 768px) {
            font-size: 2.275rem;

            margin-bottom: 10rem;
        }

        @media screen and (min-width: 1024px) {
            font-size: 2.875rem;

            margin-bottom: 10rem;
        }
    }


    &__svg {
        height: 1.5rem;
        width: 1.5rem;
        fill: $color4;
        animation: arrow 3s ease infinite;
    }

    &__rocket {
        width: 90px;
        height: 90px;

        .cls-1 {
            fill: #1d1d1b;
        }

        .cls-2 {
            fill:
                #cbe7f5;
        }

        .cls-3 {
            fill:
                #85bfe9;
        }

        .cls-4 {
            fill:
                #e7413e;
        }

        .cls-5 {
            fill:
                #fab900;
        }

        .cls-6 {
            fill:
                #f2910d;
        }
    }


    &__link {
        text-decoration: none;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;



        &:hover {
            text-decoration: none;

        }

        &:hover .header__rocket {
            animation: rocket 5s ease infinite;
        }

        &:hover .header__rocket .cls-6 {
            fill: red;
        }

        &:hover .header__rocket .cls-2 {
            fill: red;
        }

        &:hover .header__rocket .cls-3 {
            fill: #f2910d;
        }


    }
}



